<template>
    <div class="contenedor">
        <span class="titulo">Mi banco</span>
           <p>Tipo de cuenta: {{ cuenta }}</p>
           <p>Saldo: {{saldo}}</p>
           <p>Cuenta {{ estado ? 'activo' : 'desactivada'}}</p>
        <div v-for="(servicio, index) in servicios" :key="index">
            {{ index + 1}} - {{ servicio }}
        </div>
        <hr>
        <center>
            <accion class="aumentar" texto="aumentar" @evento="aumentar"/>
            <accion class="disminuir" texto="disminuir" @evento="disminuir"/>
        </center>
    </div>
</template>

<script>
import accion from './accion.vue'
export default {
    components:{
        accion
    },
    data() {
        return {
        saldo: 0,
        cuenta: 'visa',
        estado: false,
        servicios: ['Transferencias', 'Depositos', 'Retiros', 'Pagos']
        }
    },
    methods:{
        aumentar(){
            this.saldo += 50;
            this.desactivar = false
        },
        disminuir(){
            if (this.saldo === 0) {
                alert('Te quedaste sin dinero bro')
                this.desactivar = true
            }else{
                this.saldo -= 50;
            }
        }
    }
}
</script>

<style scoped>
.contenedor{
    text-align: left;
    color: blueviolet;
    font-size: 17px;
    font-weight: 700;
}
.titulo{
    text-align: center!important;
    font-size: 20px;
    font-weight: bold;
    color: red;
}
.servicios{
    color: violet;
    font-size: 14px!important;
    font-weight: 500!important;
}
.aumentar{
    background-color: #4CAF50; /* Green */
    border: none;
    border-radius: 15px;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    margin: 10px;
}
.disminuir{
    background-color: #4CAF50; /* Green */
    border: none;
    border-radius: 15px;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    margin: 10px;
}
</style>